import {
	myget,
	myrequest,
	addresupdateurl,
} from '../../api.js';
export default {
	data() {
		return {
			addressData: {
				id: '',
				uid: '',
				name: '',
				mobile: '',
				addressName: '在地图选择',
				area: '',
				default: false
			},
			
		}
	},
	async onLoad(options) {
		let title = '编辑收货地址';
		uni.setNavigationBarTitle({
			title
		})
		const data = JSON.parse(options.options);
		this.addressData.uid = await myget('uid');
		this.addressData.id = data.id;
		this.addressData.name = data.name;
		this.addressData.mobile = data.mobile;
		this.addressData.addressName = data.address;
		this.addressData.area = data.area;
		this.addressData.default = data.default;
	},
	methods: {
		switchChange(e) {
			this.addressData.default = e.detail.value;
		},

		//地图选择地址
		chooseLocation() {
			uni.chooseLocation({
				success: (data) => {
					this.addressData.addressName = data.name;
				}
			})
		},

		//提交
		async confirm() {
			let data = this.addressData;
			if (!data.name) {
				uni.showToast({
					title: '请填写收货人姓名',
					icon: 'none',
				});
				return;
			}
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.mobile)) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: 'none',
				});
				return;
			}
			if (!data.addressName) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: '请在地图选择所在位置',
				});
				return;
			}
			if (!data.area) {
				uni.showToast({
					title: '请填写门牌号信息',
					icon: 'none',
				});
				return;
			}
			this.addressData.uid = await myget('uid');
			console.log(this.addressData);
			await myrequest(addresupdateurl, 'POST', this.addressData);
			uni.navigateBack();
		},
	}
}
